<template>
  <div class="container">
    <canvas id="canv"></canvas>
    <div class="box">
      <img src="../assets/tree.jpg" alt="">
      <div class="texts">天天开心 圣诞快乐 Merry Christmas！</div>
    </div>
  </div>
</template>



<script>
export default {
  name: 'page3',

  data() {
    return {
      canvas: {},
      ctx: {},
      snows: [],
      num: 200,
      w: null,
      h: null,
      span: null,
      texts: []
    }
  },

  mounted() {
    this.canvas = document.getElementById('canv')
    this.ctx = this.canvas.getContext('2d')

    this.initText()
    this.initSnow()
    this.canvas.width = this.w
    this.canvas.height = this.h
    this.draw()
    setInterval(() => {
      this.draw()
    }, 20)
  },

  methods: {
    initSnow() {
      this.w = window.innerWidth
      this.h = window.innerHeight
      for (let i = 0; i < this.num; i++) {
        this.snows.push({
          x: Math.random() * this.w,
          y: Math.random() * this.h,
          r: Math.random() * 4 + 1
        })
      }
    },
    move() {
      for (let i = 0; i < this.num; i++) {
        let snow = this.snows[i]
        snow.x += Math.random() * 2 + 1
        snow.y += Math.random() * 2 + 1
        if (snow.x > this.w) {
          snow.x = 0
        }
        if (snow.y > this.h) {
          snow.y = 0
        }
      }
    },
    draw() {
      this.ctx.clearRect(0, 0, this.w, this.h)
      this.ctx.beginPath()
      this.ctx.fillStyle = 'rgb(255,255,255)'
      this.ctx.shadowColor = 'rgb(255,255,255)'
      this.ctx.shadowBlur = 10

      for (let i = 0; i < this.num; i++) {
        let snow = this.snows[i]
        this.ctx.moveTo(snow.x, snow.y)
        this.ctx.arc(snow.x, snow.y, snow.r, 0, Math.PI * 1)
      }
      this.ctx.fill()
      this.ctx.closePath()
      this.move()
    },
    initText() {
      this.texts = document.querySelector('.texts')
      this.span = this.texts.textContent.split('').map((item, i) => {
        return `
    <span style="transform:rotate(${
      i * 13
    }deg);position: absolute;transform-origin: 0 4.13rem;color: #fff;left: 50%;font-size: 0.5167rem">
      ${item}
    </span>
  `
      })
      this.texts.innerHTML = this.span.join('')
    }
  }
}
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
  // overflow: hidden;
}

.container {
  background-color: black;
  width: 100%;
  height: 100%;
  canvas {
    background-color: black;
    width: 100%;
    height: 100%;
  }
}

.box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 8.3333rem;
  height: 8.3333rem;
  // background-color: pink;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    position: absolute;
    height: 70%;
    border-radius: 0.1333rem;
  }
  .texts {
    width: 100%;
    height: 100%;
    font-family: '华文彩云';
    animation: animate 20s linear infinite;
  }
}

@keyframes animate {
  0% {
    transform: rotate(0);
  }
  100% {
    /* 灏辨槸缁曞湀鏃嬭浆 */
    transform: rotate(360deg);
  }
}
</style>