import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    page: 1
  },
  getters: {
  },
  mutations: {
    CHANGEPAGE() {
      this.page = window.location.hash.split('e')[1]
    }
  },
  actions: {
    changepage(context, value) {
      context.commit(CHANGEPAGE, value)
    }
  },
  modules: {
  }
})
