<template>
  <div ref='vantaRef'>
    <main class="container animated">
      <div class="inner">
        <h1>准备好了吗？</h1>
        <p>Are you ready ?</p>
        {{ $store.state.count }}
      </div>
    </main>
  </div>
</template>

<script>
import BIRDS from 'vanta/src/vanta.birds'
export default {
  mounted() {
    this.init()
    // setInterval(() => {
    //   console.log(this.i)
    // }, 200)
  },
  data() {
    return {
      i: this.index
    }
  },
  props: ['index'],
  methods: {
    init() {
      ;(this.vantaEffect = BIRDS({
        el: this.$refs.vantaRef,
        // backgroundColor: 0xffffff,
        THREE: THREE
      })),
        VANTA.BIRDS({
          el: this.$refs.vantaRef,
          backgroundColor: 0x936793,
          mouseControls: true,
          touchControls: true,
          gyroControls: false,
          minHeight: 200.0,
          minWidth: 200.0,
          scale: 1.0,
          scaleMobile: 1.0,
          color1: 0x2d014d,
          color2: 0xa400ff,
          birdSize: 1.7,
          wingSpan: 20.0,
          quantity: 3.0,
          backgroundAlpha: 0.0
        })
      setTimeout(() => {
        const main = document.querySelector('main')
        main.style.opacity = 1
        main.style.filter = 'blur(0px)'
      }, 200)
    }
  },
  beforeDestroy() {
    if (this.vantaEffect) {
      this.vantaEffect.destroy()
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  position: relative;
  height: 100vh;
}

main.animated {
  opacity: 0;
  filter: blur(0.2667rem);
  transition: opacity 1s ease-in-out, filter 1s ease-in-out;
}

h1 {
  // color: #000;
  font-size: 0.96rem;
  margin-bottom: 0.2rem;
  letter-spacing: 0.08rem;
  font-family: 'Microsoft Yahei';
}

p {
  // position: relative;
  // top: 5%;
  // font: 700 60px 'Microsoft Yahei';
  // text-align: center;
  font-size: 0.5333rem;
  // color: #000;
}

.inner {
  position: absolute;
  bottom: 42%;
  left: 5%;
}
</style> 

