<template>
  <div class="container">
    <canvas id="heart"></canvas>
    <h1 id="text"></h1>

  </div>
</template>
<script>
import { Point, Particle, ParticlePool, settings } from '@/js/heart'
export default {
  name: 'TestPage4',

  data() {
    return {}
  },

  mounted() {
    let canvas = document.getElementById('heart')
    let context = canvas.getContext('2d')
    let particles = new ParticlePool(settings.length)
    let particleRate = settings.length / settings.duration // 粒子/秒
    let time

    // 画出心对应的点 -PI <= t <= PI
    function pointOnHeart(t) {
      return new Point(
        160 * Math.pow(Math.sin(t), 3),
        130 * Math.cos(t) - 50 * Math.cos(2 * t) - 20 * Math.cos(3 * t) - 10 * Math.cos(4 * t) + 25
      )
    }

    // 使用虚拟画布创建粒子图像
    const image = (function () {
      let canvas = document.createElement('canvas'),
        context = canvas.getContext('2d')
      canvas.width = settings.size
      canvas.height = settings.size

      // 创建路径
      function to(t) {
        let point = pointOnHeart(t)
        point.x = settings.size / 2 + (point.x * settings.size) / 350
        point.y = settings.size / 2 - (point.y * settings.size) / 350
        return point
      }
      // 创建路径
      context.beginPath()
      let t = -Math.PI
      let point = to(t)

      context.moveTo(point.x, point.y)
      while (t < Math.PI) {
        t += 0.01 // 帧步数
        point = to(t)
        context.lineTo(point.x, point.y)
      }
      context.closePath()
      // 创建填充颜色
      context.fillStyle = settings.color
      context.fill()
      // 创建图像
      let image = new Image()
      image.src = canvas.toDataURL()
      return image
    })()

    // 渲染
    function render() {
      // 下一个动画帧
      requestAnimationFrame(render)

      // 更新时间
      let newTime = new Date().getTime() / 1000
      let deltaTime = newTime - (time || newTime)
      time = newTime

      // 清理画布
      context.clearRect(0, 0, canvas.width, canvas.height)

      // 创建新的粒子
      var amount = particleRate * deltaTime
      for (var i = 0; i < amount; i++) {
        var pos = pointOnHeart(Math.PI - 2 * Math.PI * Math.random())
        var dir = pos.clone().length(settings.velocity)
        particles.add(canvas.width / 2 + pos.x, canvas.height / 2 - pos.y, dir.x, -dir.y)
      }

      // 更新和绘制粒子
      particles.update(deltaTime)
      particles.draw(context, image)
    }

    // 重绘画布的大小
    function onResize() {
      canvas.width = canvas.clientWidth
      canvas.height = canvas.clientHeight
    }
    window.onresize = onResize

    // 延迟渲染
    setTimeout(function () {
      onResize()
      render()
    }, 10)
  },

  methods: {}
}
</script>

<style lang="less" scoped>
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

#heart {
  width: 750px;
  height: 750px;
  margin: 0 auto;
  animation: animate 1.2s infinite;
}

#text {
  color: white;
  font-size: 5em;
  text-align: center;
  text-shadow: 0 0 0.05em #fff, 0 0 0.2em #ee3e3e, 0 0 0.3em #f80808;
  margin-top: -0.6em;
  user-select: none;
  position: absolute;

  animation: animate 1.2s infinite;
}
@keyframes animate {
  0% {
    transform: scale(1);
  }
  30% {
    transform: scale(0.8);
  }
  60% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 750px) {
  #heart {
    transform: scale(0.5);
    animation: animate-small 1.2s infinite;
  }

  #text {
    font-size: 4em;
    margin-top: -0.3em;

    animation: animate-small 1.2s infinite;
  }

  @keyframes animate-small {
    0% {
      transform: scale(0.5);
    }
    30% {
      transform: scale(0.4);
    }
    60% {
      transform: scale(0.6);
    }
    100% {
      transform: scale(0.5);
    }
  }
}
</style>