<template>
  <div class="container">
    <div class="box">
      <li class="one" @click.stop="silde($event,'one')">
        <div class="front">
          <img src="../assets/snow.png" alt="">
        </div>
        <div class="back">1</div>
      </li>
      <li class="two" @click="silde($event,'two')">
        <div class="front">
          <img src="../assets/snow.png" alt="">
        </div>
        <div class="back"></div>
      </li>
      <li class="three" @click="silde($event,'three')">
        <div class="front">
          <img src="../assets/snow.png" alt="">
        </div>
        <div class="back"></div>
      </li>
      <li class="four" @click="silde($event,'four')">
        <div class="front">
          <img src="../assets/snow.png" alt="">
        </div>
        <div class="back"></div>
      </li>
      <li class="five" @click="silde($event,'five')">
        <div class="front">
          <img src="../assets/snow.png" alt="">
        </div>
        <div class="back"></div>
      </li>
      <li class="six" @click="silde($event,'six')">
        <div class="front">
          <img src="../assets/snow.png" alt="">
        </div>
        <div class="back"></div>
      </li>
      <li class="seven" @click="silde($event,'seven')">
        <div class="front">
          <img src="../assets/snow.png" alt="">
        </div>
        <div class="back"></div>
      </li>
      <li class="eight" @click="silde($event,'eight')">
        <div class="front">
          <img src="../assets/snow.png" alt="">
        </div>
        <div class="back"></div>
      </li>
      <li class="nine" @click="silde($event,'nine')">
        <div class="front">
          <img src="../assets/snow.png" alt="">
        </div>
        <div class="back"></div>
      </li>
    </div>
    <div class="choice">
      <p>{{choiceNum[0]}}</p>
      <p>{{choiceNum[1]}}</p>
      <p>{{choiceNum[2]}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TestPage5',

  data() {
    return {
      snows: {
        one: { value: false },
        two: { value: false },
        three: { value: false },
        four: { value: false },
        five: { value: false },
        six: { value: false },
        seven: { value: false },
        eight: { value: false },
        nine: { value: false }
      },
      messages: [1, 2, 3, 4, 5, 6, 7, 8, 9],
      count: 0,
      randomNum: [],
      choiceNum: []
    }
  },

  mounted() {
    for (let i = 1; i < 10; i++) {
      this.initNum()
    }
    console.log(this.randomNum)
    for (let i = 0; i < 9; i++) {
      let lis = document.querySelectorAll('li')
      lis[i].querySelector('.back').innerHTML = this.messages[this.randomNum[i]]
    }
  },

  methods: {
    initNum() {
      var num = Math.ceil((Math.random() * 1000000000) % 9) - 1
      if (this.randomNum.indexOf(num) == -1) {
        this.randomNum.push(num)
      } else {
        this.initNum()
      }
    },
    silde(e, v) {
      if (this.choiceNum.length < 3) {
        let text = document.querySelector('.' + v).querySelector('.back')
        this.choiceNum.push(text.innerText)
        text.style.backgroundColor = '#8cd2fc7d'
      }
      var a = 0
      this.snows[v].value = true
      Object.keys(this.snows).map(key => {
        if (this.snows[key].value == true) {
          a++
        }
      })
      this.count = a
      let li = document.querySelector('.' + v)
      li.classList.add('show')
      if (this.count == 3) {
        Object.keys(this.snows).map(key => {
          if (this.snows[key].value == false) {
            setTimeout(() => {
              this.silde('$event', key)
            }, 500)
          }
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 1% 0;
}
.box {
  list-style: none;
  max-width: 1000px;
  width: 100%;
  height: 9.7067rem;
  // max-height: 600px;
  // background-color: pink;
  // padding: 0.2667rem 0.2667rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  li {
    position: relative;
    border-radius: 0.2333rem;
    border: 0.0467rem solid rgb(254, 224, 30);
    width: 30%;
    height: 30%;
    list-style: none;
    background-color: #fff;
    display: flex;
    // overflow: hidden;
    justify-content: center;
    align-items: center;
    transform-style: preserve-3d;
    transition: all 2s;
    .front,
    .back {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      border-radius: 0.2333rem;
      font-size: 0.4rem;
      color: #000;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .front {
      width: 100%;
      height: 100%;
      z-index: 1;
      backface-visibility: hidden;
      background-color: #fff;
      img {
        height: 68%;
        transition: transform 1.6s;
      }
    }

    .back {
      background-color: #ffe6c93b;
      transform: rotateY(180deg);
    }
  }
}

.choice {
  max-width: 16rem;
  width: 100%;
  height: 4rem;
  max-height: 130px;
  background-color: pink;
  // padding: 0 20px;
  p {
    margin: 0 0 0 20px;
    color: #000;
  }
}

.box li:hover img {
  transform: scale(1.4);
  cursor: pointer;
}

.show {
  transform: rotateY(-180deg);
}
</style>